<template>
  <div class="add container">
    <div class="add__box">
      <input class="add__in" type="text" placeholder="Написать сообщение..." v-model="text" />
      <button class="button" v-if="text == ''">
        <img src="../assets/img/Frame.svg" alt="" />
      </button>
      <button type="submit" class="button" @click="addCard" v-if="text !== ''">
        <img src="../assets/img/enter.svg" alt="" />
      </button>
    </div>
    <div class="add__box">
      <input class="add__in" type="text" placeholder="Написать сообщение..." v-model="textt" />
      <button class="button" v-if="textt == ''">
        <img src="../assets/img/Frame.svg" alt="" />
      </button>
      <button type="submit" class="button" @click="addCardd" v-if="textt !== ''">
        <img src="../assets/img/enter.svg" alt="" />
      </button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      text: "",
      textt: "",
    };
  },
  methods: {
    addCard() {
      if (this.text != "") {
        this.$emit("addCard", {
          text: this.text,
          bool1: true
        });
        (this.text = "");
      }
    },
    addCardd() {
      if (this.textt != "") {
        this.$emit("addCardd", {
          textt: this.textt,
          bool1: false
        });
        (this.textt = "");
      }
    },
  },
};
</script>
<style lang=""></style>
