<template>
  <Nav />
  <Chat :cards="cards" :cards2="cards2"/>
  <Add @addCard="addCard" @addCardd="addCardd"/>
</template>
<script>

import Nav from '@/components/Nav.vue'
import Chat from '@/components/Chat.vue'
import Add from '@/components/Add.vue'
export default {
  components: {
    Nav,
    Chat,
    Add
  },
  data() {
    return {
      add: true,
      cards: [],
      id:1
    }

  },
  methods: {
    addCard(cardObj) {
      cardObj.id = this.id++;
      this.cards.push(cardObj);
    },
    addCardd(cardObj) {
      cardObj.id = this.id++;
      this.cards.push(cardObj);
    },
  }
}
</script>
